.Conversations{
    display: flex;
    flex-direction: row;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
    font-size: 16px;
    line-height: 18px;
    height: 100vh;
    background-color: white;
    margin: 0rem auto;
}

.AllChatBox{
    display: flex;
    flex-direction: column;
    width: 21rem;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
    font-size: 16px;
    line-height: 18px;
    background-color: white;
    border: 1px solid var(--lineColor);
    position: relative;
    padding-top: 3rem;
}

.TopBarMessages{
    display: flex;
    align-items: center;
    background: var(--cardColor);
    justify-content: space-between;
    left: 0;
    right: 0;
    position:absolute;
    top: 0;
    padding: 0.5rem 1rem;
    border-bottom: 1px solid rgb(183, 183, 183);
    /* z-index: 1; */
    height: 2rem;
}

.BackIcon{
    width: 28px;
    cursor: pointer;
    display: none;
}

.MessageVerticalIcon{
    width: 21px;
    cursor: pointer;
}

/* conversationlist */
.ConversationsList{
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    overflow-x: auto;
}

.Conversation{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    height: 70px;
    cursor: pointer;
    padding: 0rem 1rem;
}

.Conversation:hover{
    background-color: rgb(245, 245, 245);
}

.pic-div, .pic {
    position: relative;
    height: 50px;
    width: 50px;
    border-radius: 50%;
}

.onlineIcon{
    position: absolute;
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    background-color: #31c400;
    border: 2px solid white;
    top: -2px;
    right: -2px;
}

.chat-username {
    padding: 10px;
    margin-left: 0.5rem;
    display: flex;
    flex-direction: column;
}

.chat-username>span {
    height: 50%;
    width: auto;
}

.chat-name {
    font-weight: 500;
    font-size: 1em;
}

.chat-last-msg {
    color: #717171;
    font-size: 13px;
}

/* messagelist */
.CurrentChatBox{
    display: flex;
    flex-direction: column;
    width: 30rem;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Helvetica,Arial,sans-serif;
    font-size: 16px;
    line-height: 18px;
    background-color: white;
    border: 1px solid var(--lineColor);
    position: relative;
    padding-top: 3rem;
    padding-bottom: 4.5rem;
}

.MessagesList{
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    overflow-y: scroll;
}

.MessageIconAndTitle{
    display: flex;
    align-items: center;
    gap: 1rem;
}

.MessageProfile{
    border: 1px solid rgb(218, 218, 218);
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}

.MessageTitle{
    margin-top: -5px;
    font-weight: 600;
    font-size: 22px;
    font-family: cursive;
    cursor: pointer;
}

.SendMessageSection{
    display: flex;
    justify-content:space-between;
    align-items: center;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: white;
    padding: 1rem;
}

.SendMessageInputAndButton{
    display: flex;
    justify-content:space-between;
    align-items: center;
    height: 1.5rem;
    border-radius: 25px;
    border: 1px solid var(--lineColor);
    background-color: rgb(246, 246, 246);
    padding: 0.5rem 1rem;
    flex-grow: 1;
}

.SendMessageInput{
    font-size: 15px;
    border: none;
    outline: none;
    background: transparent;
    flex-grow: 1;
}

@media screen and (max-width:900px){
    .AllChatBox{
        width: 18rem;
    }
    .CurrentChatBox{
        width: 23rem;
    }
}
@media screen and (max-width:800px){
    .CurrentChatBox{
        width: 20rem;
    }
}

@media screen and (max-width:700px){
    .Conversations{
        margin: 0rem 0rem;
        width: 100%;
    }
    .TopBarMessages{
        position: fixed;
    }
    .SendMessageSection{
        position: fixed;
    }
    .AllChatBox{
        width: 100%;
        border: none;
    }
    .CurrentChatBox{
        width: 100%;
        border: none;
    }
    .BackIcon{
        display: flex;
    }
}


@media screen and (max-width:350px){
    .BackIcon{
        width: 26px;
    }
}